html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  font-family: 'ZCOOL QingKe HuangYou', cursive;
  letter-spacing: 0.1em;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.chat__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  height: 600px;
  height-position: relative;
  padding-top: 20px;
  background-color: #E0E0E0;
  position: relative;
}

.chat__left {
  width: 370px;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 20px;
  height: 100%;
}

.user {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
}

.user.active {
  background-color: #4FC3F7;
  color: #fff;
}

.user:hover, .user:focus {
  background-color: #4FC3F7;
}

.user__avatar, .message__avatar {
  max-width: 80px;
  margin-right: 20px;
  padding: 10px;
  border-radius: 50%;
}

.user__avatar img, .message__avatar img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.user__name {
  font-size: 20px;
}

.chat__right {
  margin-left: 370px;
  flex: 1 0 auto;
  background-color: #fff;
  max-height: 600px;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.message__window {
  flex: 1 0 auto;
  background: url(../img/bg.jpg) no-repeat;
  background-size: cover;
  display: flex;
  align-items: flex-end;
  position: relative;
  max-height: 530px;
  overflow-y: hidden;
}

.message__container {
  max-height: 100%;
  display: flex;
  flex-direction: column;
  position: absolute;
}

.message__container.hide {
  opacity: 0;
}

.message__container.show {
  opacity: 1;
}

.message__field {
  width: 90%;
  height: 50px;
  padding: 4px 20px;
  font-size: 16px;
  border-radius: 7px;
}

.message__field::placeholder {
  color: gray;
  font-size: 18px;
}

.send__btn {
  width: 5%;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #000;
  font-size: 38px;
  background: transparent;
}

.message {
  margin: 10px;
  width: auto;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

.message--bot {
  justify-content: flex-end;
}

.message--bot .message__avatar {
  order: 2;
  margin-right: 0;
  margin-left: 20px;
}

.message--bot .message__text {
  order: 1;
  background-color: #FFF9C4;
}

.message--bot .message__text:after {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 10px 0 10px 20px;
  border-color: transparent transparent transparent #FFF9C4;
  left: auto;
  right: -10px;
}

.message__container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex: 1 0 auto;
  width: 100%;
}

.message__text {
  background-color: #FFF9C4;
  color: #000;
  flex: 1 0 auto;
  font-size: 15px;
  padding: 10px;
  border-radius: 10px;
  max-width: 370px;
  position: relative;
  min-height: 40px;
}

.message__text:after {
  content: '';
  position: absolute;
  top: 50%;
  left: -10px;
  transform: translateY(-50%);
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 10px 20px 10px 0;
  border-color: transparent #FFF9C4 transparent transparent;
}
